.storage {
  padding-top: get-vw(30px);
  background-color: #f8f9fb;
  text-align: center;
  .title {
      font-size: get-vw(46px);
      font-weight: 100;
      @extend %txtGrad; }
  p {
    font-size: get-vw(16px);
    width: 47%;
    margin: 0 auto; }
  svg {
    width: get-vw(250px);
    margin-top: get-vw(50px); }
  .btn {
    display: inline-block;
    font-size: get-vw(18px);
    width: get-vw(350px);
    border-radius: 50px;
    padding: get-vw(20px) 0;
    position: relative;
    overflow: hidden;
    margin: 0 get-vw(30px);
    @extend %relative;
    text-transform: uppercase;


    &--drugstore {
      border: 2px solid #FA963D;
      color: #FA963D;
      position: relative;
      transition: background 0.3s ease-in-out, color 0.3s ease-in-out; }

    &--instr {
      @extend %relative;
      color: #fff;
      background-image: linear-gradient(to right, #e76558 0%,#fa963d 100%, #e76558 100%);
      background-size: 200% auto;
      box-shadow: 0 3px 6px rgba(231,101,88,0.5);
      transition: background 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
      &:hover {
        background-position: 100% 50%; } } } }


.btn-box {
  padding: get-vw(95px) 0 get-vw(170px) 0; }




