.question {
    background-image: linear-gradient(to right, #e76558, #fa963d);
    height: 375px;
    width: 100%;
    position: relative;
    .title {
        font-size: get-vw(36px);
        margin-bottom: 0;
        font-weight: 100;
        color: #292c35; }
    p {
        font-size: get-vw(18px);
        font-weight: 100;
        color: #7d8691; }

    &-wrapper {
        position: relative;
        top: get-vw(50px);
        background-color: #fff;
        display: grid;
        grid-template-columns: 50% 50%;
        grid-template-rows: auto;
        grid-template-areas: 'left right';
        @extend %bordRadius;
        box-shadow: 2px 18px 57px -20px rgba(41,44,53,0.3); }
    &__left {
        grid-area: left;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto;
        padding: get-vw(35px) 0  get-vw(35px)  get-vw(35px);
        &-bottom {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            grid-template-rows: auto;
            padding-top: get-vw(50px);
            grid-template-areas: 'memory stress dream';
            p {
                font-size: get-vw(16px); }
            sup {
                color: #e76558;
                font-weight: 600;
                cursor: pointer; } } }
    &__memory {
        grid-area: memory; }
    &__dream {
        grid-area: dream; }
    &__stress {
        grid-area: stress; }

    &__right {
        grid-area: right;
        justify-self: center;
        align-self: center;
        img {
            width: get-vw(485px);
            height: auto; } }
    .first {
        position: relative;
        &:after {
            content: "";
            position: absolute;
            width: get-vw(400px);
            height: 1px;
            background-color: #efeff2;
            bottom: get-vw(-30px);
            left: 0; } } }





