@media screen and (max-width: 756px) {
    .instruction {
        padding-left: 0;
        &-wrapper {
            width: 95%;
            border-radius: 5px; }
        .title {
            font-size: get-vw(150px); }
        .heading, span {
            font-size: get-vw(100px); }
        p {
            font-size: get-vw(80px); }
        &-rect {
            width: get-vw(1700px);
            height: get-vw(2720px);
            right: get-vw(30px);
            top: get-vw(100px);
            border-radius: 5px; } } }
